//
// Core: Main Header
//

@include header-variant(
  $lte-header-light-bg,
  $lte-header-light-color,
  $lte-header-mobile-light-bg,
  $lte-header-mobile-light-color
);

.main-header {
  z-index: $lte-zindex-main-header;
  grid-area: main-header;
  border-bottom: $lte-main-header-bottom-border;

  .nav-link {
    position: relative;
    height: $nav-link-height;
  }
}

body[data-sidebar-position="right"] {
  direction:rtl;

  .navbar-nav {
    padding-right: 0;
    padding-left: unset;
  }

  .menu-item-chat
  .menu-item-notifications,
  .menu-item-userprofile ul {
    right: auto;
    left: 0;
  }
}

// Navbar badge
.navbar-badge {
  position: absolute;
  top: 9px;
  right: 5px;
  padding: 2px 4px;
  font-size: .6rem;
}

